import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import IconButton from '@material-ui/core/IconButton/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Dialog from '@material-ui/core/Dialog/Dialog';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import Typography from '@material-ui/core/Typography/Typography';
import TextField from '@material-ui/core/TextField/TextField';
import Button from '@material-ui/core/Button/Button';
import Send from '@material-ui/icons/Send';
import LinearProgress from '@material-ui/core/LinearProgress';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';

import { getActiveUserData, isAddress } from '../utils/crypto';

import Transition from './Transition';

import { hideSendToken, reloadAccount } from '../actions/wallet';
import { showSnackbar } from '../actions/snackbar';

import { transfer } from '../utils/mixin';
import { withStyles } from '@material-ui/core/styles';
import { SNACKBAR_ERROR } from './WalletSnackbar';

const styles = {
  cssLabel: {
    color: '#fff',
    '&$cssFocused': {
      color: '#fff',
    },
  },
  cssFocused: {},
  cssUnderline: {
    color: '#fff',
    '&:after': {
      borderBottomColor: '#fff',
    },
  },
  cssOutlinedInput: {
    color: '#fff',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
      textAlign: 'center',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
    textAlign: 'center',
  },
};

class SendToken extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sendTo: '',
      sendAmount: '',
      sendMemo: '',
      sendPin: '',
      isLoading: false,
      selectedAsset: props.assets[0].asset_id,
    };
  }

  handleAmountChange = event => {
    const formattedAmount = event.target.value
      .replace(/^0+(?!\.|$)/, '')
      .replace(/[^0-9 .]+/g, '')
      .replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1');

    this.setState({ [event.target.name]: formattedAmount });
  };

  handlePinChange = event => {
    let value = event.target.value;
    value = value.replace(/\D/g, ''); // numeric only
    if (value.length > 6) {
      value = value.substring(0, 6);
    }
    this.setState({ [event.target.name]: value });
  };

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  disableSubmit = () => {
    return !this.isSendValid();
  };

  isSendValid = () => {
    const { activeAccount, assets } = this.props;
    const { address } = activeAccount;
    const {
      sendTo,
      sendAmount,
      isLoading,
      selectedAsset,
      sendPin,
    } = this.state;
    const asset = assets.find(a => a.asset_id === selectedAsset);

    return (
      !isLoading &&
      sendTo &&
      isAddress(sendTo) &&
      asset &&
      asset.balance &&
      parseFloat(asset.balance) >= parseFloat(sendAmount) &&
      sendAmount > 0 &&
      sendTo !== address &&
      sendPin &&
      !(sendPin.length < 6)
    );
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  sendToken = async () => {
    const { sendTo, sendAmount, sendMemo, sendPin, selectedAsset } = this.state;
    this.setState({ isLoading: true });

    const {
      assets,
      showSnackbar,
      hideSendToken,
      activeAccount,
      reloadAccount,
    } = this.props;
    const asset = assets.find(a => a.asset_id === selectedAsset);
    const userData = await getActiveUserData(activeAccount);
    const sendData = {
      sendTo,
      sendAmount,
      sendMemo,
      sendPin,
    };

    try {
      showSnackbar('Submitting transfer transaction, please wait...');
      await transfer(asset, userData, sendData);
      this.setState({
        isLoading: false,
        sendTo: '',
        sendAmount: '',
        sendMemo: '',
        sendPin: '',
      });
      reloadAccount();
      showSnackbar('Transfer successfully!');
      hideSendToken();
    } catch (error) {
      this.setState({ isLoading: false });
      console.error(error);
      showSnackbar(`Transfer failed: ${error.description}`, SNACKBAR_ERROR);
    }
  };

  renderSending() {
    if (this.isSending()) {
      return (
        <div>
          <p>Submitting send transaction, please wait...</p>
          <LinearProgress />
        </div>
      );
    }

    return null;
  }

  render() {
    const { open, hideSendToken, activeAccount, assets, classes } = this.props;
    if (!activeAccount) {
      return null;
    }

    const {
      sendTo,
      sendAmount,
      sendMemo,
      selectedAsset,
      sendPin,
      showPassword,
    } = this.state;

    if (!assets || assets.length === 0) {
      return null;
    }

    return (
      <Dialog
        fullScreen
        open={open}
        onClose={hideSendToken}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="appBar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={hideSendToken}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              Transfer
            </Typography>
          </Toolbar>
        </AppBar>
        <div className="dialog">
          <div className="card send-card">
            <div className="send-card-container">
              <TextField
                select
                label="Token"
                name="selectedAsset"
                className="send-token"
                value={selectedAsset}
                onChange={this.handleChange}
                helperText="Please select the token to send"
                margin="normal"
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                  },
                }}
                InputProps={{
                  width: 20,
                  classes: {
                    root: classes.cssOutlinedInput,
                    notchedOutline: classes.notchedOutline,
                  },
                  //className: classes.textField,
                }}
                variant="outlined"
              >
                {assets.map(asset => (
                  <MenuItem key={asset.asset_id} value={asset.asset_id}>
                    <div className="send-asset-container">
                      <img
                        src={asset.icon_url}
                        alt={asset.symbol}
                        className="send-asset-icon"
                      />
                      <div className="send-asset-info">
                        {asset.balance || 0} {asset.symbol}
                      </div>
                    </div>
                  </MenuItem>
                ))}
              </TextField>
              <TextField
                required
                name="sendTo"
                label="To User ID on Mixin"
                className="send-to"
                autoComplete="off"
                value={sendTo}
                onChange={this.handleChange}
                margin="normal"
                disabled={this.isSending()}
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                  },
                }}
                InputProps={{
                  width: 20,
                  classes: {
                    root: classes.cssOutlinedInput,
                    notchedOutline: classes.notchedOutline,
                  },
                  //className: classes.textField,
                }}
                variant="outlined"
              />
              <TextField
                required
                name="sendAmount"
                label="Amount"
                className="send-amount"
                value={sendAmount}
                onChange={this.handleAmountChange}
                margin="normal"
                type="number"
                placeholder="0.00"
                disabled={this.isSending()}
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                  },
                }}
                InputProps={{
                  width: 20,
                  classes: {
                    root: classes.cssOutlinedInput,
                    notchedOutline: classes.notchedOutline,
                  },
                }}
                variant="outlined"
              />
              <TextField
                name="sendMemo"
                label="Memo"
                className="send-memo"
                value={sendMemo}
                onChange={this.handleChange}
                margin="normal"
                disabled={this.isSending()}
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                  },
                }}
                InputProps={{
                  width: 20,
                  maxLength: 140,
                  classes: {
                    root: classes.cssOutlinedInput,
                    notchedOutline: classes.notchedOutline,
                  },
                }}
                variant="outlined"
              />
              <FormControl className="send-pin" margin="normal">
                <TextField
                  required
                  name="sendPin"
                  autoComplete="off"
                  type={showPassword ? 'text' : 'password'}
                  value={sendPin}
                  onChange={this.handlePinChange}
                  InputLabelProps={{
                    classes: {
                      root: classes.cssLabel,
                    },
                  }}
                  InputProps={{
                    width: 20,
                    maxLength: 6,
                    classes: {
                      root: classes.cssOutlinedInput,
                      notchedOutline: classes.notchedOutline,
                    },
                  }}
                  variant="outlined"
                  placeholder="Your 6 digits PIN"
                  disabled={this.isSending()}
                />
              </FormControl>
              <Button
                id="send-token-button"
                className="send-token-button actionbutton"
                variant="contained"
                color="secondary"
                disabled={this.disableSubmit()}
                onClick={this.sendToken}
              >
                Transfer <Send className="send-button-icon" />
              </Button>
              {this.renderSending()}
            </div>
          </div>
        </div>
      </Dialog>
    );
  }
}

const mapStateToProps = state => ({
  open: state.wallet.sendTokenOpen,
  activeAccount: state.account.activeAccount,
  assets: state.account.assets,
});

const mapDispatchToProps = {
  hideSendToken,
  showSnackbar,
  reloadAccount,
};

SendToken.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(SendToken));
